import { Component, ChangeDetectionStrategy, ViewChild } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

@Component({
  selector: 'app-pedidos',
  templateUrl: 'pedidos.component.html',
  changeDetection: ChangeDetectionStrategy.Default,
  styleUrls : ['pedidos.css']
})

export class PedidosComponent {

  

  //Modal automatica
  @ViewChild('autoShownModal') public autoShownModal:ModalDirective;
  public isModalShown:boolean = false;
 
  public showModal():void {
    this.isModalShown = true;
  }
 
  public hideModal():void {
    this.autoShownModal.hide();
  }
 
  public onHidden():void {
    this.isModalShown = false;
  }

  private data:any;
  public productList:any;

  //public myModal;
  @ViewChild('myModal') public myModal:ModalDirective;
  @ViewChild('myModalStatus') public myModalStatus:ModalDirective;

  objAEditar: any;
  objAEliminar: any;
  objAAgregar = {
    nombre : '',
    imagen : ''
  };
  eliminar_id: any;
  eliminar_nombre: any;

  public loading = false;
  public editando = false;
  public agregando = false;
  public mostrar = true;

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

  selectedObj: any;

  public viendo = false;
  public productList2:any;

  public status = null;
  public statusModal = null;
  public pedido_id = null;

    constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService,
      public fb: FormBuilder) {
      
      

    }


    ngOnInit(): void {

      this.checkLogin();
      
      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'pedidos?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);

             this.data=data;
             var dia : any;
             var mes : any;
             var anio : any;
             var fecha : any;
             var hora : any;
             var minutos : any;
             var segundos : any;


             for (var i = 0; i < this.data.pedidos.length; i++) {
               fecha = new Date(this.data.pedidos[i].created_at);
               //this.data.pedidosHoy[i].created_at = new Date(this.data.pedidosHoy[i].created_at);
               dia = fecha.getDate();
               mes = fecha.getMonth() + 1;
               anio = fecha.getFullYear();

               hora = fecha.getHours();
               minutos = fecha.getMinutes();
               segundos = fecha.getSeconds();

               this.data.pedidos[i].fecha = dia+'/'+mes+'/'+anio;
               this.data.pedidos[i].hora = hora+':'+minutos+':'+segundos;

               if (this.data.pedidos[i].estado == 1) {
                 this.data.pedidos[i].estado = 'Pending';
               }else if (this.data.pedidos[i].estado == 2) {
                 this.data.pedidos[i].estado = 'Payment';
               }else if (this.data.pedidos[i].estado == 3) {
                 this.data.pedidos[i].estado = 'Delivery';
               }else if (this.data.pedidos[i].estado == 4) {
                 this.data.pedidos[i].estado = 'Cancel';
               }
             }

             setTimeout(()=>{
               this.productList = this.data.pedidos;
               this.filteredItems = this.productList;
               this.init();

               this.loading = false;
             },1000);
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;

              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

    checkLogin(){
      /*Verifiacar si hay un usario logeado y su tipo*/
      var user = localStorage.getItem('luvas_user');
      var user_login = JSON.parse(user);

      if (user_login) {
        if (user_login.tipo_usuario != 1) {
          this.router.navigate(['luvas/home']);
        }
      }else{
        this.router.navigate(['luvas/home']);
      }
    }

    //Alerta cuando se vence el token
    ok_alerta(): void {
      this.router.navigate(['pages']);
    }

    cerrarAlerta(): void {
      this.alerta = false
    }

    ver(obj): void {
      this.loading = true;

      this.http.get(this.rutaService.getRutaApi()+'pedidos/'+obj.id+'?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);

             this.data=data;
             this.selectedObj = this.data.pedido;

             if (this.data.pedido.estado == 1) {
               this.data.pedido.estado = 'Pending';
             }else if (this.data.pedido.estado == 2) {
               this.data.pedido.estado = 'Payment';
             }else if (this.data.pedido.estado == 3) {
               this.data.pedido.estado = 'Delivery';
             }else if (this.data.pedido.estado == 4) {
               this.data.pedido.estado = 'Cancel';
             }

             if (this.data.pedido.metodo_pago == 1) {
               this.data.pedido.metodo_pago = 'Transferencia';
             }else if (this.data.pedido.metodo_pago == 2) {
               this.data.pedido.metodo_pago = 'PayPal';
             }

             this.viendo = true;
             this.loading = false;
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;

              }
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

    atras(): void {
      this.viendo = false;
      this.selectedObj = null;
      this.objAEliminar = null;
      this.status = null;
      this.statusModal = null;
      this.pedido_id = null;

    }

    aEliminar(): void {
      this.objAEliminar = this.selectedObj;
      //console.log(this.objAEliminar);
      this.eliminar_id = this.objAEliminar.id;
      //this.eliminar_nombre = this.objAEliminar.usuario.nombre;
      //this.myModal.show();
    }

    eliminar(): void {
      console.log(this.objAEliminar);
      
      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token')
      }

      this.http.delete(this.rutaService.getRutaApi()+'pedidos/'+this.eliminar_id+'?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              //----<
              if (this.productList) {
                var aux = this.productList;
                this.productList = [];

                for (var i = 0; i < aux.length; ++i) {
                  if (aux[i].id != this.eliminar_id) {
                     this.productList.push(aux[i]);
                  }
                  
                }

                this.filteredItems = this.productList;
                this.init();
              }
              
              //---->

              this.loading = false;
              this.atras();

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
              
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;
              }
              //no encontrada o confilto
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

    /*Tratar el status seleccionado*/
    cambiarStatus(status): void {
      this.status = status;
      this.pedido_id = this.selectedObj.id;
      if (status == 'Delivery') {
        this.statusModal = 'enviar';
      }else if (status == 'Cancel') {
        this.statusModal = 'cancelar';
      }

      //console.log(this.status);
    }

    /*Actualiar el status*/
    actualizarStatus(): void {

      var estado;
      if (this.status == 'Delivery') {
        estado = 3;
      }else if (this.status == 'Cancel') {
        estado = 4;
      }
      
      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token'),
        estado: estado
      }

      this.http.put(this.rutaService.getRutaApi()+'pedidos/'+this.pedido_id+'?token='+localStorage.getItem('luvas_token'), datos)
         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              //----<

                for (var i = 0; i < this.productList.length; ++i) {
                  if (this.productList[i].id == this.pedido_id) {
                     this.productList[i].estado = this.status;
                  }                  
                }

                this.filteredItems = this.productList;
                this.init();
              
              //---->

              this.selectedObj.estado = this.status;

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);
              
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;
              }
              //no encontrada o confilto
              else { 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }


   //Tabla----<
   filteredItems : any;
   pages : number = 4;
   pageSize : number = 5;
   pageNumber : number = 0;
   currentIndex : number = 1;
   items: any;
   pagesIndex : Array<number>;
   pageStart : number = 1;
   inputName : string = '';

   init(){
         this.currentIndex = 1;
         this.pageStart = 1;
         this.pages = 4;

         this.pageNumber = parseInt(""+ (this.filteredItems.length / this.pageSize));
         if(this.filteredItems.length % this.pageSize != 0){
            this.pageNumber ++;
         }
    
         if(this.pageNumber  < this.pages){
               this.pages =  this.pageNumber;
         }
       
         this.refreshItems();
         console.log("this.pageNumber :  "+this.pageNumber);
   }

   FilterByName(){
      this.filteredItems = [];
      if(this.inputName != ""){
            for (var i = 0; i < this.productList.length; ++i) {
              if (this.productList[i].usuario.nombre.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }else if (this.productList[i].fecha.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }
              else if (this.productList[i].hora.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }
              else if (this.productList[i].estado.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }
              else if (this.productList[i].usuario.email.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }
              /*else if (this.productList[i].id.indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }*/
            }

            // this.productList.forEach(element => {
            //     if(element.nombre.toUpperCase().indexOf(this.inputName.toUpperCase())>=0){
            //       this.filteredItems.push(element);
            //    }
            // });
      }else{
         this.filteredItems = this.productList;
      }
      console.log(this.filteredItems);
      this.init();
   }
   fillArray(): any{
      var obj = new Array();
      for(var index = this.pageStart; index< this.pageStart + this.pages; index ++) {
                  obj.push(index);
      }
      return obj;
   }
   refreshItems(){
       this.items = this.filteredItems.slice((this.currentIndex - 1)*this.pageSize, (this.currentIndex) * this.pageSize);
       this.pagesIndex =  this.fillArray();
   }
   prevPage(){
      if(this.currentIndex>1){
         this.currentIndex --;
      } 
      if(this.currentIndex < this.pageStart){
         this.pageStart = this.currentIndex;
      }
      this.refreshItems();
   }
   nextPage(){
      if(this.currentIndex < this.pageNumber){
            this.currentIndex ++;
      }
      if(this.currentIndex >= (this.pageStart + this.pages)){
         this.pageStart = this.currentIndex - this.pages + 1;
      }
 
      this.refreshItems();
   }
    setPage(index : number){
         this.currentIndex = index;
         this.refreshItems();
    }
    //Tabla---->


  
}



